import { useEffect, useState } from 'react';
import ReactPlayer from 'react-player/lazy';
// import video from "../.mp4";

const Video = (props) => {
    console.log("words: ", props.word)
    const [isWindow, setIsWindow] = useState(false);
    const [playIndex, setPlayIndex] = useState(0);
    const [links, setLinks] = useState();

    useEffect(() => {
        setIsWindow(true);
        if (props) {
            setLinks(props.word)
        }
    }, [links, props])

    const handleNextVideo = () => {
        if (!props) return;
        if(playIndex === props.word.length - 1){
            setPlayIndex(0);
        } else {
            setPlayIndex(playIndex + 1);
            // setLink(props.word[playIndex])
        }
    }


    return (
        <>
            {isWindow && props.length!==0 &&
                <ReactPlayer
                    className='react-player'
                    // url={process.env.PUBLIC_URL + '/.mp4'}    //  url
                    // url={`${process.env.PUBLIC_URL}/tmp_video/${props.word}.mp4`}
                    url=
                    {links ? links[playIndex] : undefined}

                    // url={`http://localhost:8000/tmp_video/${props.word}`}
                    // url={`http://localhost:8000/tmp_video/?words=${props.word}`}    //  url
                    width='700px'         //   ()
                    height='466px'        //   ()
                    playing={true}        //   on
                    muted={true}          //  on
                    controls={true}       //    
                    pip={true}            // pip   
                    onEnded={() => {handleNextVideo()}}
                    // onEnded={() => console.log("ending")}  //    
                />
            }
        </>
    )
}

export default Video;